import {createStore,combineReducers} from 'redux'
import ListeReducer from './reducers/liste'
import CanliReducer from './reducers/Canli_visible'
import VideoReducer from './reducers/Video_sayfası'
import LoginReducer from './reducers/Login'
import kullaniciReducer from './reducers/Kullanici'
import Mac_listeReducer from'./reducers/Mac_liste'
import soket_Reducer from'./reducers/Soket.io'
import Soket_key_reducer from'./reducers/Soket_key'
 const reducers=combineReducers({
     Rliste:ListeReducer,
     Rcanli:CanliReducer,
     Rvideo:VideoReducer,
     Rkullanici:kullaniciReducer,
     Rlogin:LoginReducer,
     Rmac_liste:Mac_listeReducer,
     Rsoket:soket_Reducer,
     RKey_soket:Soket_key_reducer,
 })
 const store=createStore(reducers)

 export default store