import { w3cwebsocket as W3CWebSocket } from "websocket";
import Image from "material-ui-image";
import React, { useState, useEffect } from "react";
import { Button, Paper, Typography, Grid } from "@material-ui/core";
import AppBar from "@material-ui/core/AppBar";
import { useSelector, useDispatch } from "react-redux";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import { video_visibility } from "../store/actions/Video_sayfası";
import { canli_visibility } from "../store/actions/Canli_visible";
import { Login_sayfası_visible } from "../store/actions/Login";
import { liste_ekle, liste_silme } from "../store/actions/liste";
import $ from "jquery";
import Cookies from 'universal-cookie';
<script src="https://code.jquery.com/jquery-3.5.1.min.js"></script>;
const useStyles = makeStyles((theme) => ({
  paper: {
    margin: theme.spacing(1),
    width: 400,
    height: 400,
  },
  
  buttonContainer: {},
  title: {
    flexGrow: 1,
  },
}));
const dönüştür = async (data) => {
  return new Promise((resolve) => {
    resolve((data = JSON.parse(data)));
  });
};

export default function Canli_yayin() {
  const classes = useStyles();
  const [url, seturl] = useState("");
  const state = useSelector((state) => state);
  const dispatch = useDispatch();
  const [soket, setsoket] = useState("");
  useEffect(() => {
    console.log(state.Rmac_liste);
    const client = new W3CWebSocket("ws:///localhost:8889");
    setsoket(client);
    client.onopen = () => {
      console.log("WebSocket Client Connected");
      let veriler = [];
      let message = { message: "bağlan" };
      veriler.push(message);
      veriler.push(state.RKey_soket)
      for (var e = 0; e < state.Rmac_liste.length; e++) {
        let json = { isim: "" + state.Rmac_liste[e] + "" };
        veriler.push(json);
      }
   //   console.log(JSON.stringify(veriler));

      client.send(JSON.stringify(veriler));
    };
    const dönüştür = async (data) => {
      return new Promise((resolve) => {
        resolve((data = JSON.parse(data)));
      });
    };
    let urlObject;
    client.onmessage = async (message) => {
      if(message.data==="log_out"){
        window.location.reload();
      }
      else{
     // console.log(message.data)
      let sonuc = await dönüştür(message.data);
      // console.log(new Blob([new Uint8Array(sonuc.blob.data,  sonuc.uzunluk,sonuc.uzunluk)]));

      if (urlObject) {
        URL.revokeObjectURL(urlObject);
      }

      urlObject = URL.createObjectURL(
        new Blob([new Uint8Array(sonuc.blob.data, sonuc.uzunluk)])
      );
      // urlObject = URL.createObjectURL(message.data);
     // var contents = $("#M" + sonuc.mac);

      // contents.src=urlObject.toString()
     
      document.querySelector("#M" + sonuc.mac).src=urlObject.toString()
      
      // seturl(urlObject);
    }
    };
  }, []);

  const Video_Sayfası = () => {
    dispatch(video_visibility(true));
    dispatch(canli_visibility(false));
    dispatch(liste_silme());
    let veriler = [];
    let message = { message: "kop" };
    veriler.push(message);
    veriler.push(state.RKey_soket)
    for (var e = 0; e < 2; e++) {
      let json = { isim: "" + state.Rmac_liste[e] + "" };
      veriler.push(json);
    }
   // console.log(JSON.stringify(veriler));

    soket.send(JSON.stringify(veriler));
    soket.close();
  };
  const cıkıs = () => {
    try {
      fetch("http://localhost:80/api", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          type: "log_out",
          mail: "abc@gmail.com",
          cookie:document.cookie
          .split('; ')
          .find(row => row.startsWith('otomatik='))
          .split('=')[1]
        }),
      })
        .then((response) => response.json())
        .then(async (json) => {
          //let sonuc = await dönüştür(json);
          
          if (json.result === false) {
          } 
          else {
            const cookies = new Cookies();
           cookies.set('otomatik', "vjd495vt849mokg5ytfmöfoe48tufmömvsvsv56s4v6sdlgmdbv1cs665s4vsğvössv4s6v46s", { path: '/' });
           dispatch(canli_visibility(false));
           dispatch(video_visibility(false));
           dispatch(Login_sayfası_visible(true));
           soket.close();
           state.Rsoket.disconnect();
           window.location.reload();
          }
        });
    } catch (error) {
      console.log(error);
    }
    
  };
  return (
    <div>
      <AppBar position="static">
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            Enelsis
          </Typography>
          <Button onClick={Video_Sayfası} color="inherit">
            Video Kaydı
          </Button>

          <Button onClick={cıkıs} color="inherit">
            Çıkış Yap
          </Button>
        </Toolbar>
      </AppBar>
      <Grid container spacing={2}>
        <Grid item xs={12}>
          <Grid container spacing={2}>
            {state.Rmac_liste.map((genre, i) => {
              return (
                <Grid key={i} item>
                  <Paper  className={classes.paper}>
                    <img id={"M" + genre} width="400" height="400"></img>
                  </Paper>
                </Grid>
              );
            })}
          </Grid>
        </Grid>
      </Grid>
    </div>
  );
}
