import { Mac_liste_ekle,Mac_liste_temizle } from "../types";



const reducer = (state = [], action) => {
  switch (action.type) {
    case Mac_liste_ekle:
        
      return  [...state,action.pyload]
     case Mac_liste_temizle:
        return [];
    default:
      return state;
  }
};
export default reducer;