import { liste_ekleme,CLEAR_THINGS } from "../types";

const default_state = [];

const reducer = (state = [], action) => {
  switch (action.type) {
    case liste_ekleme:
        
      return  [...state,action.pyload]
     case CLEAR_THINGS:
        return [];
    default:
      return state;
  }
};
export default reducer;