import { w3cwebsocket as W3CWebSocket } from "websocket";

import React, { useState, useEffect } from "react";
import {
  Button,
  Typography,
  Paper,
  Grid,
  TextField,
  Dialog,
  DialogActions,
  DialogContent,
  DialogTitle,
} from "@material-ui/core";
import Snackbar from "@material-ui/core/Snackbar";
import MuiAlert from "@material-ui/lab/Alert";
import AppBar from "@material-ui/core/AppBar";
import { useSelector, useDispatch } from "react-redux";
import Toolbar from "@material-ui/core/Toolbar";
import { makeStyles } from "@material-ui/core/styles";
import { liste_ekle, liste_silme } from "../store/actions/liste";
import { canli_visibility } from "../store/actions/Canli_visible";
import { video_visibility } from "../store/actions/Video_sayfası";
import { Scrollable, useScrollable } from "@vdjurdjevic/material-scrollbars";
import io from "socket.io-client";
import $ from "jquery";
import { Create_Soket_io } from "../store/actions/Soket_io";
import Cookies from "universal-cookie";
import { Login_sayfası_visible } from "../store/actions/Login";
import { soket_key } from "../store/types";
import { soket_key_ayarla, soket_keytemizle } from "../store/actions/Sokey_key";

let listeleme_fonsiyon;
<script src="https://code.jquery.com/jquery-3.5.1.min.js"></script>;

function Alert(props) {
  return <MuiAlert elevation={6} variant="filled" {...props} />;
}
const x = document.createElement("VIDEO");
const useStyles = makeStyles((theme) => ({
  root: {
    flexGrow: 1,
  },
  paper: {
    margin: theme.spacing(1),
    width: "40%",
    height: "20%",
  },
  buttonContainer: {},
  title: {
    flexGrow: 1,
  },
  listeici: {
    paddingTop: 5,
    paddingLeft: 15,
    paddingRight: 15,
    paddingBottom: 5,
  },
  liste: {
    paddingTop: "2%",
    paddingLeft: 10,
    paddingRight: 10,
  },
}));

const video_load = (gelen) => {
  x.setAttribute("src", gelen);
  //x.onloadstart = function () {};
  x.setAttribute("width", "200");
  x.setAttribute("height", "200");
  x.setAttribute("controls", "controls");
  document.querySelector("#video").append(x);
};

export default function Video_Kaydi() {
  const [hataopen, sethataOpen] = useState(false);
  const [tip, settip] = useState("error");
  const [hata_mesaj, sethata_mesaj] = useState("");
  const [Şifre_değişiklik, setŞifre_değişiklik] = useState(false);
  const [E_şifre, setE_şifre] = useState("");
  const [Y_şifre, setY_şifre] = useState("");
  const [YT_şifre, setYT_şifre] = useState("");
  const classes = useStyles();
  const state = useSelector((state) => state);
  const dispatch = useDispatch();
  const [deneme, setdeneme] = useState("");
  const dönüştür = async (data) => {
    return new Promise((resolve) => {
      resolve((data = JSON.parse(data)));
    });
  };

   

  useEffect(() => {
    function mac_tıklandı(mac_name){
    
      dispatch(liste_silme());
      state.Rsoket.open();
      let json = {
        "cookie":state.RKey_soket.cookie,
        "key":state.RKey_soket.key,
        "mac":mac_name,
      };
     
     
      state.Rsoket.emit("set_mac_file", json);
      state.Rsoket.emit("video_list_request", json);
    };
    async function listeleme_fonsiyon (data)  {
      // let sonuc = await dönüştür(data);
     
       dispatch(liste_silme());
       
       data.map((obj) => {
         dispatch(liste_ekle(obj.isim));
       });
     };
    let json = JSON.stringify(state.RKey_soket);
    state.Rsoket.emit("io", json);

    state.Rsoket.on("video_list_response", async (data) => {
      console.log("mesaj geldi")
      dispatch(liste_silme());
      listeleme_fonsiyon(data);
    });
    state.Rsoket.on("video", (data) => {
      video_load("http://localhost:80/video" + data.url);
    });
    state.Rsoket.on("error_message", (data) => {
      dispatch(liste_silme());
      sethata_mesaj(data.message);
      settip("error");
      fhata_mesajı(1500);
    });
    state.Rsoket.on("log_out", (data) => {
      sethata_mesaj("Giriş Yapınız");
      settip("error");
      fhata_mesajı(100);
      window.location.reload();
    });

 
         document
      .querySelector("#add-message")
      .addEventListener("click", function (e) {
        
        let json = {
          "cookie":state.RKey_soket.cookie,
          "key":state.RKey_soket.key,
          "video":e.target.innerHTML,
        };
        
        state.Rsoket.emit("show_video", json);
      });
    document
      .querySelector("#Mac_listesi")
      .addEventListener("click", function (e) {
        mac_tıklandı(e.target.innerHTML);
      });
      return () => {
        console.log("kjfnfhnldmhkdlhm")
        

      };
  }, []);

  const Canli_Sayfası = () => {
   
    state.Rsoket.disconnect();
    dispatch(canli_visibility(true));
    dispatch(video_visibility(false));
  };
   
  const cıkıs = () => {
    try {
      fetch("http://localhost:80/api", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          type: "log_out",
          mail: "abc@gmail.com",
          cookie: document.cookie
            .split("; ")
            .find((row) => row.startsWith("otomatik="))
            .split("=")[1],
        }),
      })
        .then((response) => response.json())
        .then(async (json) => {
          
          //let sonuc = await dönüştür(json);

          if (json.result === false) {
          } else {
            const cookies = new Cookies();
            cookies.set(
              "otomatik",
              "vjd98539tym8fuldsgfmho845urmjssv4ssv6ssmvsvsv56s4v6sdlgmdbv1cs665s4vsğvössv4s6v46s",
              { path: "/" }
            );
            dispatch(canli_visibility(false));
            dispatch(video_visibility(false));
            dispatch(Login_sayfası_visible(true));
            state.Rsoket.disconnect();
            window.location.reload();
          }
        });
    } catch (error) {
      console.log(error);
    }
  };

  const fhata_mesajı = ( time) => {
    sethataOpen(true);

    setTimeout(function () {
      sethataOpen(false);
    }, time);
  };
  const sifre_degis = () => {
    var Tbody = {
      type: "changePassword",
      newPassword: Y_şifre,
      oldPassword: E_şifre,
      cookie:state.RKey_soket.cookie,
      key:state.RKey_soket.key,
    };
    try {
      const body = Tbody;
      fetch("http://localhost:80/api", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(body),
      })
        .then((response) => response.json())
        .then(async(json) => {
          //let sonuc = await dönüştür(json);
          console.log(json)
        //  console.log(json);
          if (json.success === true) {
            sethata_mesaj(json.message);
            settip("success");
            sethataOpen(true);
            setTimeout(function () {
              sethataOpen(false);
            }, 2000);

            setŞifre_değişiklik(false);
          } else {
            sethata_mesaj(json.message);
            settip("error");
            sethataOpen(true);
            setTimeout(function () {
              sethataOpen(false);
            }, 2000);
            setŞifre_değişiklik(false);
          }
        });
    } catch (error) {
     // console.log(error);
    }
  };
  const şifre_dialog = () => {
    setŞifre_değişiklik(true);
  };
  const VŞifreClose = () => {
    //  console.log(E_şifre);
    setŞifre_değişiklik(false);
    setE_şifre("");
    setYT_şifre("");
    setY_şifre("");
  };
  const OŞifreClose = () => {
    if (YT_şifre !== "" && Y_şifre !== "") {
      if (YT_şifre === Y_şifre) {
        // console.log("şifreler aynı");
        setE_şifre("");
        setYT_şifre("");
        setY_şifre("");
       sifre_degis();
      } else {
        // console.log("şifreler farklı");
        setE_şifre("");
        setYT_şifre("");
        setY_şifre("");
        sethata_mesaj("Şifreler Farklı");
        settip("error");
        sethataOpen(true);
        setTimeout(function () {
          sethataOpen(false);
        }, 2000);
      }
    } else {
      sethata_mesaj("Yeni Şifre veya ŞifreTekrarı Boş Bırakılamaz");
      settip("error");
      sethataOpen(true);
      setTimeout(function () {
        sethataOpen(false);
      }, 2000);
    }
  };
  return (
    <div className={classes.root}>
      <AppBar position="static" id="root">
        <Toolbar>
          <Typography variant="h6" className={classes.title}>
            Enelsis
          </Typography>
          <Button onClick={Canli_Sayfası} color="inherit">
            Canli Yayin
          </Button>
          <Button onClick={şifre_dialog} color="inherit">
            Şifre Değiştir
          </Button>
          <Button onClick={cıkıs} color="inherit">
            Çıkış Yap
          </Button>
        </Toolbar>
      </AppBar>
      <Grid container spacing={1}>
        <Grid
          item
          xs={6}
          sm={3}
          className={classes.liste}
          container
          justify="space-around"
          alignItems="baseline"
          direction="column-reverse"
        >
          
          <Paper variant="elevation" elevation={2}>
            <ul id="Mac_listesi">
              {state.Rmac_liste.map((genre, i) => {
                
                return (
                  <Typography
                    component="h3"
                    className={classes.listeici}
                    style={{ fontFamily: "Montserrat", color: "#37474F" }}
                    variant="h5"
                  >
                    
                    <li key={i}>{genre}</li>
                  </Typography>
                );
              })}
            </ul>
          </Paper>
        </Grid>

        <Grid item xs={6} sm={4}>
          <Grid
            className={classes.liste}
            container
            justify="space-around"
            alignItems="baseline"
            direction="column-reverse"
          >
            <Paper
              id="paper2"
              variant="elevation"
              elevation={2}
              style={{ maxHeight: 400, overflow: "auto" }}
            >
              <ul id="add-message">
                {state.Rliste.map((genre, i) => {
                  
                  return (
                    <Typography
                      component="h3"
                      className={classes.listeici}
                      style={{ fontFamily: "Montserrat", color: "#37474F" }}
                      variant="h5"
                    >
                      <li key={i}>{genre}</li>
                    </Typography>
                  );
                })}
              </ul>
            </Paper>
          </Grid>
        </Grid>

        <Grid item xs={6} sm={3}>
          <Grid
            className={classes.liste}
            container
            justify="space-around"
            alignItems="baseline"
            direction="column-reverse"
          >
            <Paper
              id="video"
              variant="elevation"
              elevation={2}
              style={{ paddingTop: "2%", width: "100%" }}
            ></Paper>
          </Grid>
        </Grid>
      </Grid>
      <Snackbar
        anchorOrigin={{ vertical: "top", horizontal: "right" }}
        open={hataopen}
        autoHideDuration={6000}
      >
        <Alert severity={tip}>{hata_mesaj}</Alert>
      </Snackbar>
      <Dialog open={Şifre_değişiklik} aria-labelledby="form-dialog-title">
        <DialogTitle id="form-dialog-title">
          Şifre Değişiklik Ekranı
        </DialogTitle>
        <DialogContent>
          <TextField
            autoFocus
            margin="dense"
            id="E_Ş"
            label="Şuanki Şifrenizi Giriniz"
            fullWidth
            onChange={(e) => setE_şifre(e.target.value)}
            value={E_şifre}
          />
          <TextField
            margin="dense"
            id="Y_Ş"
            label="Yeni Şifrenizi Giriniz"
            fullWidth
            onChange={(e) => setY_şifre(e.target.value)}
            value={Y_şifre}
          />
          <TextField
            margin="dense"
            id="YT_Ş"
            label="Yeni Şifrenizi Tekrar Giriniz"
            fullWidth
            onChange={(e) => setYT_şifre(e.target.value)}
            value={YT_şifre}
          />
        </DialogContent>
        <DialogActions>
          <Button onClick={VŞifreClose} color="primary">
            Vazgeç
          </Button>
          <Button onClick={OŞifreClose} color="primary">
            Onayla
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}
