import { kullanici } from "../types";

const default_state = {
      username:"",
      name:"",
      surname:"",
      kullanici_id:"",
      kullanici_sifre:"",
      kullnıcı_mac:[]
     
};

const reducer = (state = default_state, action) => {
  
  switch (action.type) {
    case kullanici:
        
      return action.pyload;
     
    default:
      return state;
  }
};
export default reducer;