import { w3cwebsocket as W3CWebSocket } from "websocket";
import React, { useState, useEffect } from "react";
import Cookies from 'universal-cookie';
import { makeStyles } from "@material-ui/core/styles";
import Canli from "./components/Canli_yayin";
import Video from "./components/Video_Kaydi";
import Login from "./components/login";
import { Create_Soket_io } from "./store/actions/Soket_io";
import { useSelector, useDispatch } from "react-redux";
import { Mac_liste_ekleme, Mac_liste_silme } from "./store/actions/Mac_liste";
import { video_visibility } from "./store/actions/Video_sayfası";
import { canli_visibility } from "./store/actions/Canli_visible";
import { Login_sayfası_visible } from "./store/actions/Login";
import { soket_key_ayarla } from "./store/actions/Sokey_key";
const crypto = require("crypto");
const dönüştür = async (data) => {
  return new Promise((resolve) => {
    resolve((data = JSON.parse(data)));
  });
};

function App() {
  const [Canli_, setCanli_] = useState(false);
  const [video_, setvideo_] = useState(false);
  const [login_, setlogin_] = useState(false);
  const state = useSelector((state) => state);
  const dispatch = useDispatch();
    const[cookieValue,setcookieVlue]=useState("")
    const cookie_parser = async (data) => {
      return new Promise((resolve) => {
        try {
     setcookieVlue( document.cookie
  .split('; ')
  .find(row => row.startsWith('otomatik='))
  .split('=')[1] )
   
   resolve(cookieValue);
    } catch (error) {
      const cookies = new Cookies();
     cookies.set('otomatik', '"v56465dg1d6g41vgsc1g5g451vg55g72cx875gfssv6ssmvsvsv56s4v6sdlgmdbv1cs665s4vsğvössv4s6v46s"', { path: '/' });
     resolve(cookies);
    }
        
      });
    };
  useEffect(async() => {
     
    try {
       await cookie_parser();
      fetch("http://localhost:80/session", {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify({
          type: "sessionCheck",
          mail: "abc@gmail.com",
          cookie:document.cookie
          .split('; ')
          .find(row => row.startsWith('otomatik='))
          .split('=')[1]
        }),
        device:"web" 
      })
        .then((response) => response.json())
        .then(async (json) => {
          console.log(json)
         // let sonuc = await dönüştür(json);
          if (json.result == false) {
            const cookies = new Cookies();
            cookies.set('otomatik', '"v56465dg1d6g41vgsc1g5g451vg55g72cx875gfssv6ssmvsvsv56s4v6sdlgmdbv1cs665s4vsğvössv4s6v46s"', { path: '/' });
            dispatch(canli_visibility(false));
            dispatch(video_visibility(false));
            dispatch(Login_sayfası_visible(true));
            setCanli_(false);
            setvideo_(false);
            setlogin_(true);
          } else {
            dispatch(Create_Soket_io("http://localhost:80/io"))
            let sonuc2 = await dönüştür(json.macler);
            let key_json={
              "cookie":json.cookie,
              "key":json.key,
            };
            dispatch(soket_key_ayarla(key_json))
            dispatch(Mac_liste_silme());
            sonuc2.map((obj) => {       
              dispatch(Mac_liste_ekleme(obj.mac));
            });
            dispatch(canli_visibility(false));
            dispatch(video_visibility(true));
            dispatch(Login_sayfası_visible(false));
            setCanli_(false);
            setvideo_(true);
            setlogin_(false);
          }
       
        });
    } catch (error) {
      console.log(error);
    }
    
  }, [state.Rlogin]);
  useEffect(() => {
    if (state.Rlogin === false) {
      setCanli_(state.Rcanli);
      setvideo_(!state.Rcanli);

      //setlogin_(!state.Rcanli)
    }
  }, [state.Rcanli]);
  return (
    <div>
      {login_ && <Login />}
      {video_ && <Video />}
      {Canli_ && <Canli />}
    </div>
  );
}

export default App;
